/**
 * ------------------------------------------------------------
 * ui.select  util
 * ------------------------------------------------------------
 */

'use strict';

var selectUtil = {
    /**
     * keyvalue
     * @param target   
     * @param key      key
     * @param value    
     */
    clearDataToNUll: function (target, key, value) {
        target[key] = value || '';
    },
    /**
     * selectedkey
     * @param data          data
     * @param valueFn       selectedkey
     * @param nullValue     key
     * @param separator     key
     * @param key           key
     * @returns {string}    
     */
    getSelectedTextByKey: function(data, valueFn, nullValue, separator, key){
        var selected = data.selected;
        var value = '';
        if (selected) {
            if (data.multiple) {
                value = selected.length? selected.reduce(function (prev, next, index) {
                    var nextValue = next[key];
                    if (index != selected.length - 1) {
                        nextValue += separator
                    }
                    return prev + nextValue
                }, '') : nullValue;
            } else {
                value = valueFn(selected);
            }
        }
        return value;
    }
};

module.exports = selectUtil;


