'use strict';

var Component = require('../../../ui-base/component');
var template = require('./index.html');
var _ = require('../../../ui-base/_');

/**
 * @class Check
 * @extend Component
 * @param {object}      options.data                    = 
 * @param {string}      [options.data.name]             => 
 * @param {boolean}     [options.data.checked=false]    <=> `false``true``null`
 * @param {boolean}     [options.data.block=false]      => block
 * @param {boolean}     [options.data.readonly=false]   => 
 * @param {boolean}     [options.data.disabled=false]   => 
 * @param {boolean}     [options.data.visible=true]     => 
 * @param {string}      [options.data.class]            => class
 */
var Check = Component.extend({
    name: 'check',
    template: template,
    /**
     * @protected
     */
    config: function() {
        _.extend(this.data, {
            name: '',
            checked: false,
            block: false
        });
        this.supr();

        this.$watch('checked', function(newValue, oldValue) {
            if(oldValue === undefined)
                return;

            /**
             * @event change 
             * @property {object} sender 
             * @property {object} date 
             */
            this.$emit('change', {
                sender: this,
                checked: newValue
            });
        });
    },
    /**
     * @method check(checked) 
     * @public
     * @param  {boolean} checked true/false
     * @return {void}
     */
    check: function(checked) {
        if(this.data.readonly || this.data.disabled)
            return;

        if(checked === undefined)
            checked = !this.data.checked;
        this.data.checked = checked;

        /**
         * @event check 
         * @property {object} sender 
         * @property {boolean} checked 
         */
        this.$emit('check', {
            sender: this,
            checked: checked
        });
    }
});

module.exports = Check;


