/**
 * ------------------------------------------------------------
 * Tabs       
 * @author   sensen(rainforest92@126.com)
 * ------------------------------------------------------------
 */

'use strict';

var Component = require('../../../ui-base/component');
var template = require('./index.html');
var _ = require('../../../ui-base/_');

/**
 * @class Tabs
 * @extend Component
 * @param {object}        [options.data]                      = 
 * @param {object}        [options.data.selected=null]        <=> 
 * @param {string}        [options.data.titleTemplate=null]   @=> 
 * @param {boolean}       [options.data.readonly=false]       => 
 * @param {boolean}       [options.data.disabled=false]       => 
 * @param {boolean}       [options.data.visible=true]         => 
 * @param {string}        [options.data.class]                => class
 */
var Tabs = Component.extend({
    name: 'tabs',
    template: template,
    /**
     * @protected
     */
    config: function() {
        _.extend(this.data, {
            tabs: [],
            selected: undefined,
            titleTemplate: null
        });
        this.supr();

        this.$watch('selected', function(newValue, oldValue) {
            /**
             * @event change 
             * @property {object} sender 
             * @property {object} selected 
             */
            this.$emit('change', {
                sender: this,
                selected: newValue
            });
        });
    },
    /**
     * @method select(item) 
     * @public
     * @param  {object} item 
     * @return {void}
     */
    select: function(item) {
        if(this.data.readonly || this.data.disabled || item.data.disabled)
            return;

        this.data.selected = item;
        /**
         * @event select 
         * @property {object} sender 
         * @property {object} selected 
         */
        this.$emit('select', {
            sender: this,
            selected: item
        });
    }
});

var Tab = Component.extend({
    name: 'tab',
    template: '<div r-hide={this.$outer.data.selected !== this}>{#inc this.$body}</div>',
    /**
     * @protected
     */
    config: function() {
        _.extend(this.data, {
            title: ''
        });
        this.supr();

        if(this.$outer)
            this.$outer.data.tabs.push(this);

        if(!this.$outer.data.selected)
            this.$outer.data.selected = this;
    }
});

module.exports = Tabs;


