/**
 * ------------------------------------------------------------
 * NumberInput 
 * @author   sensen(rainforest92@126.com)
 * ------------------------------------------------------------
 */
var Input = require('../ui.input');
var template = require('./index.html');
var _ = require('../../../ui-base/_');

/**
 * @class NumberInput
 * @extend Input
 * @param {object}        [options.data]                  = 
 * @param {string}        [options.data.value=0]          <=> 
 * @param {string}        [options.data.state]            <=> 
 * @param {number}        [options.data.min]              => 
 * @param {number}        [options.data.max]              => 
 * @param {boolean}       [options.data.hideTip=false]    => 
 * @param {boolean}       [options.data.autofocus=false]  => 
 * @param {boolean}       [options.data.readonly=false]   => 
 * @param {boolean}       [options.data.disabled=false]   => 
 * @param {boolean}       [options.data.visible=true]     => 
 * @param {string}        [options.data.class]            => class
 */
var NumberInput = Input.extend({
    name: 'number.input',
    template: template,
    /**
     * @protected
     */
    config: function() {
        _.extend(this.data, {
            value: 0,
            // @inherited state: '',
            // @inherited placeholder: '',
            hideTip: false,
            min: undefined,
            max: undefined,
            autofocus: false
        });
        this.supr();

        this.$watch('value', function(newValue, oldValue) {
            // 
            if(typeof newValue === 'string')
                return this.data.value = +newValue;

            // 
            var isOutOfRange = this.isOutOfRange(newValue);
            if(isOutOfRange !== false)
                return this.data.value = isOutOfRange;

            /**
             * @event change 
             * @property {object} sender 
             * @property {number} value 
             */
            this.$emit('change', {
                sender: this,
                value: newValue
            });
        });

        this.$watch(['min', 'max'], function(min, max) {
            if(!isNaN(min) && !isNaN(max) && min - max > 0)
                throw new NumberInput.NumberRangeError(min, max);

            // 
            var isOutOfRange = this.isOutOfRange(this.data.value);
            if(isOutOfRange !== false)
                return this.data.value = isOutOfRange;
        });
    },
    /**
     * @method add(value) 
     * @public
     * @param  {number} [value=0] /
     * @return {number} value 
     */
    add: function(value) {
        if(this.data.readonly || this.data.disabled || !value)
            return;

        if(isNaN(value))
            throw new TypeError(value + ' is not a number!');

        return this.data.value += value;
    },
    /**
     * @method isOutOfRange(value) 
     * @public
     * @param {number} value 
     * @return {boolean|number} number false
     */
    isOutOfRange: function(value) {
        var min = +this.data.min;
        var max = +this.data.max;

        // min && value < min && min
        if(!isNaN(min) && value < min)
            return min;
        else if(!isNaN(max) && value > max)
            return max;
        else
            return false;
    }
}).filter({
    number: {
        get: function(value) {
            value = '' + (value || 0);
            if(this.data.format)
                return this.data.format.replace(new RegExp('\\d{0,' + value.length + '}$'), value);
            return value;
        },
        set: function(value) {
            // return (value.replace(/[^0-9\-\.]/g, ''));
            return +value;
            // return +(value.replace(/[^\d\.\-]/g, '')) || 0;
        }
    }
});

var NumberRangeError = function(min, max) {
    this.type = 'NumberRangeError';
    this.message = 'Wrong Number Range where `min` is ' + min + ' and `max` is ' + max + '!';
}
NumberRangeError.prototype = Object.create(RangeError.prototype);
NumberInput.NumberRangeError = NumberRangeError.prototype.constructor = NumberRangeError;

module.exports = NumberInput;


