'use strict';

var Component = require('../../../ui-base/component');
var template = require('./index.html');
var _ = require('../../../ui-base/_');
var Validation = require('../../../util/validation');

/**
 * @class SelectGroup
 * @extend Component
 * @param {object}            [options.data]                    = 
 * @param {object[]}          [options.data.source=[]]          <=> 
 * @param {string}            [options.data.source[].name]      => 
 * @param {boolean}           [options.data.source[].disabled=false]  => 
 * @param {boolean}           [options.data.source[].divider=false]   => 
 * @param {number}            [options.data.depth=1]            => 
 * @param {boolean}           [options.data.required]           =>  
 * @param {object}            [options.data.selected=[]]        <=  
 * @param {object[]}          [options.data.selecteds=[]]       <=> 
 * @param {string[]|number[]} [options.data.values=[]]          <=> 
 * @param {string}            [options.data.key=id]             => 
 * @param {string[]}          [options.data.placeholders=[]]    => 
 * @param {boolean}           [options.data.hideTip=false]      => 
 * @param {boolean}           [options.data.readonly=false]     => 
 * @param {boolean}           [options.data.disabled=false]     => 
 * @param {boolean}           [options.data.visible=true]       => 
 * @param {string}            [options.data.class]              => class
 * @param {object}            [options.service]                 @=> 
 */
var SelectGroup = Component.extend({
    name: 'select.group',
    template: template,
    /**
     * @protected
     */
    config: function() {
        _.extend(this.data, {
            // @inherited source: [],
            hideTip: false,
            depth: 1,
            sources: [],
            selected: undefined,
            selecteds: [],
            key: 'id',
            values: [],
            placeholders: [],
            required:false
        });
        this.supr();

        this.$watch('selected', function(newValue, oldValue) {
            /**
             * @event change 
             * @property {object} sender 
             * @property {object} selected 
             * @property {object} selecteds 
             * @property {string} key 
             * @property {string[]|number[]} values 
             */
            this.$emit('change', {
                sender: this,
                selected: newValue,
                selecteds: this.data.selecteds,
                key: this.data.key,
                values: this.data.values
            });

            this.data.tip && this.validate();
        });

        this.data.sources[0] = this.data.source;

        var $outer = this.$outer;
        if($outer && $outer instanceof Validation) {
            $outer.controls.push(this);

            this.$on('destroy', function() {
                var index = $outer.controls.indexOf(this);
                $outer.controls.splice(index, 1);
            });
        }
    },
    /**
     * @private
     */
    _onChange: function(item, level) {
        // <select2>
        // if(this.data.readonly || this.data.disabled || (item && (item.disabled || item.divider)))
        //     return;

        this.data.sources[level + 1] = item ? item.children : undefined;
        for(var i = level + 2; i < this.data.depth; i++)
            this.data.sources[i] = undefined;

        if(level === this.data.depth - 1)
            this.data.selected = item;

        /**
         * @event select 
         * @property {object} sender 
         * @property {object} selected 
         * @property {object} level 
         */
        this.$emit('select', {
            sender: this,
            selected: item,
            selecteds: this.data.selecteds,
            level: level
        });
    },
    /**
     * @method validate() required
     * @public
     * @return {object} result 
     */
    validate: function(on) {

        var data = this.data,
            result = { success: true, message: '' },
            values = this.data.values,
            depth  = this.data.depth;

        if (data.required && (values.length < depth)) {
            result.success = false;
            result.message = this.data.message || this.$trans('PLEASE_SELECT');
            this.data.state = 'error';
        } else {
            result.success = true;
            result.message = '';
            this.data.state = '';
        }
        this.data.tip = result.message;

        this.$emit('validate', {
            sender: this,
            on: on,
            result: result
        });

        return result;
    },
});

module.exports = SelectGroup;


