'use strict';

var validator = require('validator');
var bowser = require('bowser');
var Component = require('../../../ui-base/component');
var template = require('./index.html');
var _ = require('../../../ui-base/_');

/**
 * @class Input
 * @extend Component
 * @param {object}      [options.data]                = 
 * @param {string}      [options.data.title=]      => 
 * @param {string}      [options.data.type=default]   => , primary, default, info, success, warn, error
 * @param {string}      [options.data.size=normal]    => , xs, sm, lg, xl
 * @param {string}      [options.data.icon]           => ,action;
 * @param {string}      [options.data.action]         => , icon;
 * @param {string}      [options.data.link]           => 
 * @param {string}      [options.data.target=_self]   => 
 * @param {string}      [options.data.shape]          => circle, icon
 * @param {string}      [options.data.download]       => 
 * @param {boolean}     [options.data.loading=false]  => 
 * @param {boolean}     [options.data.disabled=false] => 
 * @param {boolean}     [options.data.class=false]    => 
 */
var actionIcons= {
  /*  */
  view: 'eye',
  /*  */
  send: 'send-o',
  /*  */
  undo: 'reply-all',
  /*  */
  cancel: 'arrow-circle-left',
  /*  */
  remove: 'trash',
  /*  */
  update: 'refresh',
  /*  */
  submit: 'legal',
  /*  */
  save: 'save',
  /*  */
  copy: 'copy',
  /*  */
  pass: 'success',
  /*  */
  reject: 'warning',
  /*  */
  backward: 'angle-double-left',
  /*  */
  download: 'download',
  /*  */
  upload: 'upload',
  /*  */
  search: 'search',
  /*  */
  edit: 'edit',
  /*  */
  add: 'add',
  /*  */
  link: 'link',
  /*  */
  plus: 'plus'
};


var UIButton = Component.extend({
    name: 'ui.button',
    template: template,
    config: function() {
        _.extend(this.data, {
            title: this.$trans('CONFIRM'),
            type: 'default',
            size: 'normal',
            icon: '',
            loading: false,
            disabled: false,
            actionIcons: actionIcons,
            target: '_self'
        });
        this.supr();
    },

    init: function() {
        this.supr();
        this.$watch('download', function(url) {
            if (validator.isURL(url)) {
                if (bowser.chrome) {
                    var a = document.createElement('a');
                    a.href = url;
                    a.download = url;
                    a.click();
                } else {
                    location.href = url;
                }
                this.data.download = '';
            }
        });
    },

    onClick: function(e) {
        var loading = this.data.loading;
        if (!loading) {
            this.$emit('click', {
                sender: this,
                e: e
            });
        }
        return !!this.data.link;
    },

    onMouseUp: function (e) {
        e.target.blur();
    }
});

module.exports = UIButton;



