'use strict';

var SourceComponent = require('../../../ui-base/sourceComponent');
var template = require('./index.html');
var _ = require('../../../ui-base/_');
var Check = require('../check');
var Validation = require('../../../util/validation');

/**
 * @class CheckGroup
 * @extend CheckGroup
 * @param {object}    [options.data]                    = 
 * @param {object[]}  [options.data.source=[]]          <=> 
 * @param {string}    [options.data.value='']           <=> ,separatorid
 * @param {string}    [options.data.source[].name=[]]   => 
 * @param {string}    [options.data.key=id]             => 
 * @param {string}    [options.data.separator=',']      => value
 * @param {string}    [options.data.nameKey=name]       => name
 * @param {number}    [options.data.min]                => 
 * @param {number}    [options.data.max]                => 
 * @param {boolean}   [options.data.required]           => 
 * @param {string}    [options.data.message]            => 
 * @param {boolean}   [options.data.hideTip=false]      => 
 * @param {boolean}   [options.data.block=false]        => 
 * @param {boolean}   [options.data.readonly=false]     => 
 * @param {boolean}   [options.data.disabled=false]     => 
 * @param {boolean}   [options.data.visible=true]       => 
 * @param {string}    [options.data.class]              => class
 * @param {object}    [options.service]                 @=> 
 */
var CheckGroup = SourceComponent.extend({
    name: 'check.group',
    template: template,
    /**
     * @protected
     */
    config: function() {
        this.defaults({
            // @inherited source: [],
            block: false,
            hideTip: false,
            source: [],
            min: 0,
            max: 1000,
            nameKey: 'name',
            key: 'id',
            value: '',
            separator: ','
        });
        this.supr();

        var $outer = this.$outer;
        if($outer && $outer instanceof Validation) {
            $outer.controls.push(this);

            this.$on('destroy', function() {
                var index = $outer.controls.indexOf(this);
                $outer.controls.splice(index, 1);
            });
        }
    },
    init: function() {
        this.$watch('source', function(source) {
            if (!source || !(source instanceof Array)) { return console.error('source of check.group is not an array'); }

            var key = this.data.key,
                separator = this.data.separator,
                value = this.data.value || '',
                values = value.split(separator);

            source.forEach(function(item) {
                if (values.indexOf(item[key] + '') != -1) {
                    item.checked = true;
                }
            });
        });
        this.$watch('value', function(newValue) {
            var source = this.data.source;
            if (newValue === undefined || newValue === null) return;

            if (source) {
                var key = this.data.key,
                    separator = this.data.separator,
                    value = newValue || '',
                    values = value.split(separator);
                source.forEach(function(item) {
                    if (values.indexOf(item[key] + '') != -1) {
                        item.checked = true;
                    } else {
                        item.checked = false;
                    }
                });
            }

        });
    },
    /**
     * @method validate() min, max
     * @public
     * @return {object} result 
     */
    validate: function() {
        var source = this.data.source,
            result = { success: true, message: ''},
            required = this.data.required,
            min = this.data.min ? this.data.min : required/1,
            max = this.data.max,
            checked = source.filter(function(item) { return !!item.checked; }),
            len = checked.length;

        if (len < min || len > max) {
            result.success = false;
            result.message = this.data.message || ( '(' + min + ',' + max + ']' );
            this.data.state = 'error';
        } else {
            result.success = true;
            result.message = '';
            this.data.state = '';
        }
        this.data.tip = result.message;

        this.$emit('validate', {
            sender: this,
            result: result
        });

        return result;
    },
    /**
     * method _onCheck() check,value
     * @private
     */
    _onCheck: function(item) {
        item.checked = !item.checked;

        var key = this.data.key,
            separator = this.data.separator,
            source = this.data.source,
            checkedList = source.filter(function(item) { return item.checked; }),
            ids = checkedList.map(function(item) { return item[key]; });

        this.$update('value', ids.join(separator));

        this.data.tip && this.validate();
    }
});

module.exports = CheckGroup;


