/**
 * ------------------------------------------------------------
 * Input   
 * @author   sensen(rainforest92@126.com)
 * ------------------------------------------------------------
 */

var Component = require('../../../ui-base/component');
var template = require('./index.html');
var _ = require('../../../ui-base/_');
var Validation = require('../../../util/validation');
var inputRules = require('./common/rule.js');
var inputFilters = require('./common/filter.js');

var bowser = require('bowser');

/**
 * @class Input
 * @extend Component
 * @param {object}          [options.data]                    = 
 * @param {string}          [options.data.value]              <=> 
 * @param {string}          [options.data.type]               => , 6int, float, email, urlcharpassword
 * @param {string}          [options.data.placeholder]        => 
 * @param {string}          [options.data.state]              <=> 
 * @param {number}          [options.data.maxlength]          => 
 * @param {string}          [options.data.unit]               => 
 * @param {object[]}        [options.data.rules=[]]           => 
 * @param {boolean}         [options.data.autofocus=false]    => 
 * @param {boolean}         [options.data.readonly=false]     => 
 * @param {boolean}         [options.data.disabled=false]     => 
 * @param {boolean}         [options.data.visible=true]       => 
 * @param {string}          [options.data.class]              => class
 * @param {number}          [options.data.decimalDigits]      => type=float,filter
 * @param {boolean}         [options.data.required]           => 
 * @param {boolean}         [options.data.hideTip=false]      => 
 * @param {number}          [options.data.min]                => type=int/float, type=char
 * @param {number}          [options.data.max]                => type=int/float, type=char
 * @param {string}          [options.data.message]            => 
 * @param {string}          [options.data.size]               => , sm/md/lg
 * @param {number}          [options.data.width]              => 
 */
var Input = Component.extend({
    name: 'ui.input',
    template: template,
    /**
     * @protected
     */
    config: function() {
        _.extend(this.data, {
            hideTip: false,
            value: '',
            type: 'char',
            placeholder: '',
            state: '',
            maxlength: undefined,
            unit: '',
            rules: [],
            autofocus: false,
            _eltIE9: bowser.msie && bowser.version <= 9
        });
        this.rules({
            required: false,
            byteLen: this.data.type === 'char',
            isEmail: this.data.type === 'email',
            isURL: this.data.type === 'url',
            isInt: this.data.type === 'int',
            isFloat: this.data.type === 'float',
            message: ''
        });

        this.supr();

        var $outer = this.$outer;
        if($outer && $outer instanceof Validation) {
            $outer.controls.push(this);

            this.$on('destroy', function() {
                var index = $outer.controls.indexOf(this);
                $outer.controls.splice(index, 1);
            });
        }
    },
    init: function() {
        this.$watch('required', function(value) {
            if (value) {
                this.addRule('required');
            } else {
                this.data.rules = this.data.rules.filter(function(rule) {
                    return rule.type !== 'isRequired';
                });
            }
        });
    },
    /**
     * @override 
     */
    rules: function(ruleAttris) {
        this.supr(ruleAttris);
        ['required', 'isEmail', 'isURL', 'isFloat', 'isInt', 'byteLen'].forEach(function(name) {
            this.addRule(name);
        }.bind(this));
    },
    /**
     * @protected
     */
    addRule: function(name) {
        var min = this.data.min, 
            max = this.data.max,
            message = this.data.message,
            rules = this.data.rules;

        if (name === 'required') {
            message = message || this.$trans('PLEASE_INPUT');
        }

        if (!this.data[name]) { return; }
        var rule = inputRules[name];
        if (typeof rule === 'function') {
            rules.push(rule(min, max, message));
        } else {
            var ruleCopy = _.extend({}, rule);
            message && (ruleCopy.message = message);
            rules.push(ruleCopy);
        }
    },
    /**
     * @method validate() `rules`
     * @public
     * @return {object} result 
     */
    validate: function(on) {
        var value = this.data.value ? (this.data.value + '') : '';
        var rules = this.data.rules;

        var PRIORITY = {
            'keyup': 2,
            'blur': 1,
            'submit': 0,
            '': 0
        }

        on = on || '';
        rules = rules.filter(function(rule) {
            return (rule.on || '').indexOf(on) >= 0;
        });

        var result = Validation.validate(value, rules);
        if(result.firstRule
            && !(result.firstRule.silentOn === true || (typeof result.firstRule.silentOn === 'string' && result.firstRule.silentOn.indexOf(on) >= 0)))
                this.data.tip = result.firstRule.message;
        else
            this.data.tip = '';

        // @TODO
        if(!result.success)
            this.data.state = 'error';
        // else if(PRIORITY[on] <= PRIORITY['blur'])
        //     this.data.state = 'success';
        else
            this.data.state = '';

        this.$emit('validate', {
            sender: this,
            on: on,
            result: result
        });

        return result;
    },

    /**
     * 1. type=char,;
     * 2. type=int/float, ;
     **/
    __valueFilter: function(value) {
        var type = this.data.type;

        if (type != 'char') value = (value + '').trim();
        value = (inputFilters[type] || inputFilters['default'])(value, this.data.decimalDigits);
        return value;
    },
    _onKeyUp: function($event) {
        this.validate('keyup');
        this.$emit('keyup', $event);
    },
    _onBlur: function($event) {
        this.validate('blur');
        this.$emit('blur', $event);
    },
    _onFocus: function ($event) {
        this.$emit('focus', $event);
    },
    _onChange: function($event) {
        this.validate('change');
        this.$emit('change', $event);
    },
    _onInput: function($event) {
        this.validate('input');
        this.$emit('input', $event);
    },
    _onSearch: function($event) {
        this.$emit('search', $event);
    },
});

Input.filter('type', function(val) {
    if (['int', 'float'].indexOf(val) != -1) {
        /* number, number, ++++123 */
        return 'text';
    } else if (val == 'password') {
        return 'password';
    } else {
        return 'text';
    }
});

Input.filter({
    'valueFilter': {
        get: function(val) {
            return this.__valueFilter(val);
        },
        set: function(val) {
            return this.__valueFilter(val);
        }
    }
});

module.exports = Input;



