'use strict';

var SourceComponent = require('../../../ui-base/sourceComponent');
var template = require('./tree.view.html');
var _ = require('../../../ui-base/_');

var TreeViewList = require('./tree.view.list');

/**
 * @class TreeView
 * @extend SourceComponent
 * @param {object}    [options.data]                          = 
 * @param {object[]}  [options.data.source=[]]                <=> 
 * @param {string}    [options.data.source[].name]            => 
 * @param {boolean}   [options.data.source[].open=false]      => /
 * @param {boolean}   [options.data.source[].checked=false]   => 
 * @param {boolean}   [options.data.source[].disabled=false]  => 
 * @param {boolean}   [options.data.source[].divider=false]   => 
 * @param {string}    [options.data.value=null]               <=> 
 * @param {object}    [options.data.selected=null]            <=> 
 * @param {string}    [options.data.separator=,]              => value
 * @param {string}    [options.data.itemTemplate=null]        @=> 
 * @param {boolean}   [options.data.multiple=false]           => 
 * @param {boolean}   [options.data.hierarchical=false]       @=> service
 * @param {boolean}   [options.data.readonly=false]           => 
 * @param {boolean}   [options.data.disabled=false]           => 
 * @param {boolean}   [options.data.visible=true]             => 
 * @param {string}    [options.data.class]                    => class
 * @param {object}    [options.service]                       @=> 
 */
var TreeView = SourceComponent.extend({
    name: 'tree.view',
    template: template,
    /**
     * @protected
     */
    config: function() {
        _.extend(this.data, {
            // @inherited source: [],
            key: 'id',
            nameKey: 'name',
            value: null,
            selected: null,
            multiple: false,
            hierarchical: false
        });
        this.supr();
        this.$ancestor = this;
        this.$watch('selected', function(newVal) {
          var key = this.data.key, nameKey = this.data.nameKey, separator = this.data.separator;
          if (!newVal) return this.data.value = '';
          if (Array.isArray(newVal)) return this.data.value = newVal.map(function(d) {
              return d[key] || d[nameKey];
          }).join(separator);
          this.data.value = newVal[key] || newVal[nameKey];
        });
    },
    /**
     * @method select(item) 
     * @public
     * @param  {object} item 
     * @return {void}
     */
    select: function(item) {
        if(this.data.readonly || this.data.disabled || item.disabled || item.divider)
            return;

        if(this.data.multiple)
            return item.selected = !item.selected;

        this.data.selected = item;
        /**
         * @event select 
         * @property {object} sender 
         * @property {object} selected 
         */
        this.$emit('select', {
            sender: this,
            selected: item
        });
    },
    /**
     * @method toggle(item,open) /
     * @public
     * @param  {object} item 
     * @param  {object} open /
     * @return {void}
     */
    toggle: function(item, open) {
        if(this.data.readonly || this.data.disabled || item.disabled || item.divider)
            return;

        if(open === undefined)
            open = !item.open;
        item.open = open;

        /**
         * @event toggle 
         * @property {object} sender 
         * @property {object} item 
         * @property {boolean} open /
         */
        this.$emit('toggle', {
            sender: this,
            item: item,
            open: open
        });
    },
    /**
     * @private
     */
    _getSelected: function(source) {
        var self = this;
        if (!source) return [];
        var arr = [];
        source.forEach(function(d) {
            var child = d[self.data.childKey];
            if (child && child.length) {
              arr = arr.concat(self._getSelected(d[self.data.childKey]));
            } else if (d.checked) {
              arr = arr.concat(d);
            }
        });
        return arr;
    },
    setSelected: function(event) {
        this.data.selected = this._getSelected(this.data.source);
    }
});


