'use strict';

var Dropdown = require('../../navigation/dropdown');
var template = require('./index.html');
var _ = require('../../../ui-base/_');
var Validation = require('../../../util/validation');
var util = require('./common/util');
var Multiple = require('./plugins/multiple');
var PrivateMethod = require('./plugins/private.method');
var tip = require('../../widget/tooltip');

require('../check');

/**
 * @class Select
 * @extend Dropdown
 * @param {object}            [options.data]                          = 
 * @param {object[]}          [options.data.source=[]]                <=> 
 * @param {string}            [options.data.source[].name]            => 
 * @param {boolean}           [options.data.source[].disabled=false]  => 
 * @param {string}            [options.data.source[].tip]             => 
 * @param {string}            [options.data.source[].placement]       => 
 * @param {function}          [options.data.filter]                   =>  source  item  filter(item)  true 
 * @param {boolean}           [options.data.source[].divider=false]   => 
 * @param {object}            [options.data.selected]                 <=> 
 * @param {string|number}     [options.data.value]                    <=> 
 * @param {string}            [options.data.key=id]                   => 
 * @param {string}            [options.data.nameKey=name]             => name
 * @param {string}            [options.data.placeholder=]        => `placeholder`
 * @param {boolean}           [options.data.hideTip=false]            => 
 * @param {boolean}           [options.data.required=false]           => 
 * @param {boolean}           [options.data.readonly=false]           => 
 * @param {boolean}           [options.data.disabled=false]           => 
 * @param {boolean}           [options.data.visible=true]             => 
 * @param {string}            [options.data.class]                    => class
 * @param {object}            [options.service]                       @=> 
 * @param {boolean}           [options.data.canSearch=false]          => 
 * @param {boolean}           [options.data.isCaseSensitive=false]    => 
 * @param {boolean}           [options.data.noMatchText=]       => 
 * @param {Number}            [options.data.delaySearch=300]          => 
 * @param {Number}            [options.data.maxShowCount=1000]        => 
 * @param {boolean}           [options.data.multiple=false]           => 
 * @param {string}            [options.data.separator=,]              => value
 * @param {boolean}           [options.data.selectedClose=true]       =>  
 * @param {boolean}           [options.data.canSelectAll=false]       => 
 * @param {string}            [options.data.size]                     => , sm/md/lg
 * @param {number}            [options.data.width]                    => 
 */

var Select = Dropdown.extend({
    name: 'ui.select',
    template: template,
    /**
     * @protected
     */
    config: function () {
        var data = this.data;
        _.extend(data, {
            hideTip: false,
            selected: undefined,
            key: 'id',
            nameKey: 'name',
            value: undefined,

            // 
            searchValue: '',
            canSearch: undefined,
            filter: null,
            // 
            isCaseSensitive: true,
            noMatchText: this.$trans('NO_MATCH'),
            delaySearch: 300,
            maxShowCount: 1000,

            separator: ',',
            multiple: false,
            selectedClose: false,
            canSelectAll: true,

            placeholder: this.$trans('PLEASE_SELECT'),
            required: false
        });
        if (data.multiple && !Array.isArray(data.selected)) {
            data.selected = data.selected ? [data.selected] : [];
        }
        this.supr();

        this.$watch('selected', function (newValue, oldValue) {

            //source source0source
            if (oldValue === undefined && !(Array.isArray(data.source) && data.source.length)) {
                return;
            }
            data.value = this.getValue();
            if (!newValue && data.multiple) {
                data.selected = [];
            }
            /**
             * @event change 
             * @property {object} sender 
             * @property {object} selected 
             * @property {string} key 
             * @property {string|number} value 
             */
            this.$emit('change', {
                sender: this,
                selected: newValue,
                key: data.key,
                value: data.value
            });
        });

        this.$watch('value', function (newValue) {
            var source = data.source;
            var key = data.key;
            if (newValue === undefined || newValue === null) {
                return data.selected = newValue;
            }

            if (source) {
                if (data.multiple) {
                    var newValueArr = (newValue + '').split(data.separator);
                    data.selected = newValue ? source.filter(function (item) {
                            return newValueArr.indexOf(item[key] + '') !== -1;
                        }, this) : [];
                } else {
                    data.selected = source.find(function (item) {
                        return item[key] + '' === newValue + '';
                    }, this);
                }

                this.data.tip && this.validate();
            }
        });

        this.$watch('source', function (newValue, oldValue) {

            if (newValue === undefined) {
                return data.selected = undefined;
            }

            if (!(newValue instanceof Array)) {
                throw new TypeError('`source` is not an Array!');
            }

            var key = data.key;
            var nameKey = data.nameKey;
            var value = data.value;
            var itemHandleFn = function (value) {
                return value;
            };
            if (newValue.length) {
                if (typeof newValue[0] === 'string' || typeof newValue[0] === 'number') {
                    itemHandleFn = function (value) {
                        var item = {};
                        item[key] = value;
                        item[nameKey] = value;
                        return item
                    }
                } else if (!newValue[0].hasOwnProperty(key)) {
                    itemHandleFn = function (value) {
                        if (!value.hasOwnProperty('divider')) {
                            value[key] = value[nameKey];
                        }
                        return value
                    }
                } else if (!newValue[0].hasOwnProperty(nameKey)) {
                    itemHandleFn = function (value) {
                        value[nameKey] = value[key];
                        return value
                    }
                }
                newValue = data.source = newValue.map(function (value) {
                    return itemHandleFn(value);
                });
            }

            if (data.multiple) {
                if (value !== undefined && value !== null) {
                    var valueArr = (value + '').split(data.separator);
                    data.selected = [].concat(value ? newValue.filter(function (item) {
                            return valueArr.indexOf(item[key] + '') !== -1;
                        }, this) || [] : []);
                } else if (data.selected) {
                    data.selected = data.selected.filter(function (item) {
                        return newValue.indexOf(item) !== -1
                    });
                }
            } else {
                if (value !== undefined && value !== null) {
                    data.selected = newValue.find(function (item) {
                        return item[key] == value;
                    }, this);
                } else if (data.selected && newValue.indexOf(data.selected) < 0) {
                    data.selected = undefined;
                }

                // placeholder
                if (!data.placeholder && !data.selected) {
                    data.selected = newValue[0];
                }
            }


            /**
             * 1.isCanSearchtrue
             * 2.20 
             */
            var canSearch;
            if(this.hasOwnProperty('__canSearch')){
                canSearch = this.__canSearch;
            }else{
                canSearch = this.__canSearch = data.canSearch;
            }
            data.canSearch = (canSearch === true) || (Array.isArray(newValue) && newValue.length > 20 && canSearch !== false);
        });

        this.$watch('multiple', function (newValue, oldValue) {
            if (oldValue === undefined) {
                return;
            }
            if (newValue) {
                if (!Array.isArray(data.selected)) {
                    data.selected = data.selected ? [data.selected] : data.selected;
                }
            } else {
                data.value = '';
            }
        });
        if (this.service && this.service.getList) {
            var $updateSource = _.throttle(this.$updateSource.bind(this), data.delaySearch);
            this.$watch('searchValue', function (newValue, oldValue) {
                if (oldValue === undefined) {
                    return
                }
                $updateSource()
            });
        }

        var $outer = this.$outer;
        if ($outer && $outer instanceof Validation) {
            $outer.controls.push(this);

            this.$on('destroy', function () {
                var index = $outer.controls.indexOf(this);
                $outer.controls.splice(index, 1);
            });
        }
    },
    /**
     * @method select(item) 
     * @public
     * @param  {object} item 
     * @return {void}
     */
    select: function (item) {
        var data = this.data;
        var multiple = data.multiple;
        if (data.readonly || data.disabled ||
            (item && (item.disabled || item.divider))) {
            return;
        }
        if (multiple) {
            var selected = data.selected;
            var indexOf = this.indexOf(selected, item);
            if (indexOf !== -1) {
                selected.splice(indexOf, 1);
            } else {
                if (item) {
                    selected.push(item);
                } else {
                    selected.length = 0;
                }
            }
        } else {
            data.selected = item;
        }

        /**
         * @event select 
         * @property {object} sender 
         * @property {object} selected 
         */
        this.$emit('select', {
            sender: this,
            selected: item
        });
        if (multiple) {
            this.searchInputFocus();
            if (!data.selectedClose && item) {
                data.canSearch && this.clearSearchValue();
                return
            }
        }
        this.toggle(false);
    },
    toggle: function (open, e) {
        e && e.stopPropagation();
        var data = this.data;
        data.canSearch && this.clearSearchValue();
        this.supr(open);
    },
    validate: function (on) {
        var data = this.data;

        var result = {success: true, message: ''},
            value = this.data.value;

        value = ( typeof value == 'undefined' ) ? '' : value + '';
        if (data.required && !value.length) {
            result.success = false;
            result.message = data.message || this.$trans('PLEASE_SELECT');
            data.state = 'error';
        } else {
            result.success = true;
            result.message = '';
            data.state = '';
        }
        data.tip = result.message;

        this.$emit('validate', {
            sender: this,
            on: on,
            result: result
        });

        return result;
    }
}).use(Multiple).use(PrivateMethod);

module.exports = Select;



