var Component = require('../../../ui-base/component');
var template = require('./index.html');
var _ = require('../../../ui-base/_');
var Validation = require('../../../util/validation');

var bowser = require('bowser');

/**
 * @class TextArea
 * @extend Component
 * @param {object}        [options.data]                    = 
 * @param {string}        [options.data.value]              <=> 
 * @param {string}        [options.data.placeholder]         => 
 * @param {string}        [options.data.state]              <=> 
 * @param {number}        [options.data.maxlength]          => 
 * @param {object[]}      [options.data.rules=[]]           => 
 * @param {boolean}       [options.data.autofocus=false]    => 
 * @param {number}        [options.data.height=120]         => 
 * @param {boolean}       [options.data.required=false]     => 
 * @param {string}        [options.data.message='']         => 
 * @param {boolean}       [options.data.hideTip=false]      => 
 * @param {boolean}       [options.data.readonly=false]     => 
 * @param {boolean}       [options.data.disabled=false]     => 
 * @param {boolean}       [options.data.visible=true]       => 
 * @param {string}        [options.data.class]              => class
 * @param {string}        [options.data.size]               => , sm/md/lg
 * @param {number}        [options.data.width]              => 
 */
var TextArea = Component.extend({
    name: 'ui.textarea',
    template: template,
    /**
     * @protected
     */
    config: function() {
        _.extend(this.data, {
            hideTip: false,
            value: '',
            placeholder: '',
            state: '',
            maxlength: undefined,
            height: 120,
            rules: [],
            autofocus: false,
            _eltIE9: bowser.msie && bowser.version <= 9,
            required: false
        });

        this.supr();

        var $outer = this.$outer;
        if($outer && $outer instanceof Validation) {
            $outer.controls.push(this);

            this.$on('destroy', function() {
                var index = $outer.controls.indexOf(this);
                $outer.controls.splice(index, 1);
            });
        }
    },
    init: function() {
        this.$watch('required', function(value) {
            var rules = this.data.rules,
                message = this.data.message;

            if (value) {
                rules.push({type:'isRequired', message: message});
            } else {
                this.data.rules = rules.filter(function(rule) {
                    return rule.type != 'isRequired';
                });
            }
        });
    },
    /**
     * @method validate() `rules`
     * @public
     * @return {object} result 
     */
    validate: function(on) {
        var value = this.data.value;
        var rules = this.data.rules;

        var PRIORITY = {
            'keyup': 2,
            'blur': 1,
            'submit': 0,
            '': 0
        }

        on = on || '';
        rules = rules.filter(function(rule) {
            return (rule.on || '').indexOf(on) >= 0;
        });

        var result = Validation.validate(value, rules);
        if(result.firstRule
            && !(result.firstRule.silentOn === true || (typeof result.firstRule.silentOn === 'string' && result.firstRule.silentOn.indexOf(on) >= 0)))
                this.data.tip = result.firstRule.message;
        else
            this.data.tip = '';

        // @TODO
        if(!result.success)
            this.data.state = 'error';
        // else if(PRIORITY[on] <= PRIORITY['blur'])
        //     this.data.state = 'success';
        else
            this.data.state = '';

        this.$emit('validate', {
            sender: this,
            on: on,
            result: result
        });

        return result;
    },
    _onKeyUp: function($event) {
        this.validate('keyup');
        this.$emit('keyup', $event);
    },
    _onBlur: function($event) {
        this.validate('blur');
        this.$emit('blur', $event);
    }
});

module.exports = TextArea;



