/**
 * ------------------------------------------------------------
 * Row -
 * ------------------------------------------------------------
 */

'use strict';

var Component = require('../../../../ui-base/component');
var _ = require('../../../../ui-base/_');
var template = require('./index.html');

/**
 * @class Row
 * @extend Component
 * @param {object}          [options.data]                        => 
 * @param {string}          [options.data.type='']              =>  flex
 * @param {string}          [options.data.justify='start']      => flex 
 * @param {string}          [options.data.align='top']          => flex 
 * @param {string}          [options.data.wrap='wrap']          => flex,wrap/nowrap/wrap-reverse
 * @param {number}          [options.data.gutter='0']           => 
 */
var Row = Component.extend({
  name: 'ui.row',
  template: template,
  config: function (data) {
    this.defaults({
      type: '',
      justify: 'start',
      align: 'top',
      gutter: 0,
      wrap: 'wrap',
    });

    this.supr(data);
  },
});

Row.directive('gutter', function(ele, value) {
  this.$watch(value, function(gutter) {
    if (gutter) {
      var margin = '-' + (gutter/2) + 'px';
      ele.style.marginLeft = margin;
      ele.style.marginRight = margin;
    }
  })
});

module.exports = Row;



