'use strict';

var Component = require('../../../ui-base/component');
var template = require('./index.html');
var _ = require('../../../ui-base/_');

/**
 * @class Modal
 * @extend Component
 * @param {object}            [options.data]                      =  | Binding Properties
 * @param {string}            [options.data.title=]            =>  | Title of Dialog
 * @param {string}            [options.data.content]              => 
 * @param {string}            [options.data.contentTemplate]      => 
 * @param {string}            [options.data.footerTemplate]       => 
 * @param {boolean}           [options.data.okDisabled=false]     => Disale 
 * @param {boolean}           [options.data.cancelDisabled=false] => Disale 
 * @param {boolean}           [options.data.hasFooter=true]       =>  footer
 * @param {boolean}           [options.data.isCanClose=true]      => 
 * @param {string|boolean}    [options.data.okButton=true]        => `string`
 * @param {string|boolean}    [options.data.cancelButton=false]   => `string`
 * @param {string}            [options.data.class]                => class
 * @param {boolean}           [options.data.noClose]              => ok
 * @param {number}            [options.data.minHeight]            => 
 * @param {number}            [options.data.maxHeight]            => 
 */
var Modal = Component.extend({
    name: 'modal',
    template: template,
    /**
     * @protected
     */
    config: function() {
        _.extend(this.data, {
            title: this.$trans('NOTICE'),
            content: '',
            okButton: true,
            with: 400,
            cancelButton: false,
            noClose: false,
            okDisabled: false,
            cancelDisabled: false,
            hasFooter: true,
            isCanClose: true,
        });
        this.supr();
    },
    /**
     * @protected
     */
    init: function() {
        this.supr();

        // document.body
        if(this.$root === this)
            this.$inject(document.body);
    },
    /**
     * @method close(result) 
     * @public
     * @param  {boolean} result 
     * @return {void}
     */
    close: function(result, event) {
        /**
         * @event close 
         * @property {boolean} result 
         */
        this.$emit('close', {
            result: result
        });
        result ? this.ok(event) : this.cancel();
    },
    /**
     * @method ok() 
     * @public
     * @return {void}
     */
    ok: function(event) {
        /**
         * @event ok 
         */
        this.$emit('ok', event);
        !this.data.noClose && this.destroy();
    },
    /**
     * @method cancel() 
     * @public
     * @return {void}
     */
    cancel: function() {
        /**
         * @event cancel 
         */
        this.$emit('cancel');
        this.destroy();
    },
    _onDragStart: function($event) {
        var dialog = $event.proxy;
        dialog.style.left = dialog.offsetLeft + 'px';
        dialog.style.top = dialog.offsetTop + 'px';
        dialog.style.zIndex = '1000';
        dialog.style.position = 'absolute';
    }
});

/**
 * @method alert(content[,title]) alert
 * @static
 * @public
 * @param  {string} [content] 
 * @param  {string} [title=] 
 * @return {Modal} modal 
 */
Modal.alert = function(content, title, okButton) {
    var modal = new this({
        data: {
            content: content,
            title: title,
            okButton: okButton
        }
    });

    return modal;
}

/**
 * @method confirm(content[,title]) confirm
 * @static
 * @public
 * @param  {string} [content] 
 * @param  {string} [title=] 
 * @return {Modal} modal 
 */
Modal.confirm = function(content, title, okButton, cancelButton) {
    var modal = new this({
        data: {
            content: content,
            title: title,
            okButton: okButton,
            cancelButton: cancelButton || true
        }
    });

    return modal;
}

// var oldExtend = Modal.extend;
// Modal.extend = function() {
//     var extended = oldExtend.apply(this, arguments);
//     extended.alert = this.alert;
//     extended.confirm = this.confirm;
//     return extended;
// }

module.exports = Modal;



