'use strict';

var Component = require('../../../ui-base/component');
var template = require('./index.html');
var _ = require('../../../ui-base/_');

/**
 * @class Gotop
 * @param {object}        [options.data]                          = 
 * @param {string}        [options.data.position=bottomright]     => `topcenter``topleft``topright``bottomcenter``bottomleft``bottomright``static`
 * @param {boolean}       [options.data.disabled=false]           => 
 * @param {boolean}       [options.data.visible=true]             => 
 * @param {string}        [options.data.class]                    => class
 */
var Gotop = Component.extend({
    name: 'gotop',
    template: template,
    /**
     * @protected
     */
    config: function() {
        _.extend(this.data, {
            position: 'bottomright'
        });
        this.supr();
    },
    /**
     * @method gotop() 
     * @public
     * @return {void}
     */
    gotop: function() {
        if(this.data.disabled)
            return;

        document.body.scrollTop = 0;
    }
});

module.exports = Gotop;


