'use strict';

var Dropdown = require('../../navigation/dropdown');
var Validation = require('../../../util/validation');
var template = require('./index.html');
var _ = require('../../../ui-base/_');
var Treeview = require('../common/tree.view');

/**
 * @class TreeSelect
 * @extend Select
 * @param {object}          [options.data]                          = 
 * @param {object[]}        [options.data.source=[]]                <=> 
 * @param {string}          [options.data.source[].name]            => 
 * @param {boolean}         [options.data.source[].disabled=false]  => 
 * @param {boolean}         [options.data.source[].divider=false]   => 
 * @param {string}          [options.data.key=id]                   => 
 * @param {string}          [options.data.nameKey=name]             => name
 * @param {string}          [options.data.childKey=children]        => 
 * @param {string}          [options.data.value=null]               <=> 
 * @param {object}          [options.data.selected=null]            <=> 
 * @param {string}          [options.data.separator=,]              => value
 * @param {string}          [options.data.placeholder=]      => 
 * @param {boolean}         [options.data.hierarchical=false]       @=> service
 * @param {boolean}         [options.data.readonly=false]           => 
 * @param {boolean}         [options.data.multiple=false]           => 
 * @param {boolean}         [options.data.disabled=false]           => 
 * @param {boolean}         [options.data.visible=true]             => 
 * @param {string}          [options.data.class]                 => class
 * @param {object}          [options.service]                       @=> 
 */

// TODO: value 
var TreeSelect = Dropdown.extend({
    name: 'tree.select',
    template: template,
    config: function(data) {
        _.extend(this.data, {
            // @inherited source: [],
            // @inherited open: false,
            multiple: false,
            value: null,
            selected: null,
            separator: ',',
            placeholder: this.$trans('PLEASE_SELECT'),
            key: 'id',
            nameKey: 'name',
            childKey: 'children',
            hierarchical: false,
            updateAuto: false
        });
        this.supr();

        var $outer = this.$outer;
        if($outer && $outer instanceof Validation) {
            $outer.controls.push(this);

            this.$on('destroy', function() {
                var index = $outer.controls.indexOf(this);
                $outer.controls.splice(index, 1);
            });
        }
    },
    computed: {
        phShow: function(data) {
            if (!data.selected) return data.placeholder;
            if (Array.isArray(data.selected)) return data.selected.map(function(d) {
                return d[data.nameKey];
            }).join(data.separator);
            return data.selected[data.nameKey];
        }
    }
});

module.exports = TreeSelect;


