/**
 * ------------------------------------------------------------
 * Uploader  
 * @author   sensen(rainforest92@126.com)
 * ------------------------------------------------------------
 */

'use strict';

var Component = require('../../../ui-base/component');
var template = require('./index.html');
var _ = require('../../../ui-base/_');

var SIZE_UNITS = {
    'kB': 1000,
    'MB': 1000*1000,
    'GB': 1000*1000*1000
}

/**
 * @class Uploader
 * @extend Component
 * @param {object}            [options.data]                    = 
 * @param {string}            [options.data.title]              => 
 * @param {string}            [options.data.url]                => 
 * @param {string}            [options.data.dataType=json]      => `text``xml``json``script`
 * @param {object}            [options.data.data]               => 
 * @param {string}            [options.data.name=file]          => name
 * @param {string|string[]}   [options.data.extensions]         => `,`'png,jpg,gif'['png', 'jpg', 'gif']
 * @param {string|number}     [options.data.maxSize]            => `kB``MB``GB`
 * @param {boolean}           [options.data.disabled=false]     => 
 * @param {boolean}           [options.data.visible=true]       => 
 * @param {string}            [options.data.class]              => class
 */
var Uploader = Component.extend({
    name: 'uploader',
    template: template,
    /**
     * @protected
     */
    config: function() {
        _.extend(this.data, {
            title: '',
            url: '',
            contentType: 'multipart/form-data',
            dataType: 'json',
            data: {},
            name: 'file',
            extensions: null,
            maxSize: '',
            _sending: false,
            _id: new Date().getTime()
        });
        this.supr();
    },
    /**
     * @method upload() 
     * @public
     * @return {void}
     */
    upload: function() {
        if(this.data.disabled || this.data._sending)
            return;

        this.$refs.file.click();
    },
    _checkExtensions: function(file) {
        if(!this.data.extensions)
            return true;

        var fileName = file.name;
        var ext = fileName.substring(fileName.lastIndexOf('.') + 1, fileName.length).toLowerCase();

        var extensions = this.data.extensions;
        if(typeof extensions === 'string')
            extensions = extensions.split(',');
        
        if(extensions.indexOf(ext) >= 0)
            return true;

        /**
         * @event error 
         * @property {object} sender 
         * @property {object} name ExtensionError
         * @property {object} message 
         * @property {object} extensions 
         */
        this.$emit('error', {
            sender: this,
            name: 'ExtensionError',
            message: '' + extensions.join(', ')+ '',
            extensions: extensions
        });

        return false;
    },
    _checkSize: function(file) {
        if(!this.data.maxSize && this.data.maxSize !== 0)
            return true;

        var maxSize;
        if(!isNaN(this.data.maxSize))
            maxSize = +this.data.maxSize;
        else {
            var unit = this.data.maxSize.slice(-2);
            if(!SIZE_UNITS[unit])
                throw new Error('Unknown unit!');

            maxSize = this.data.maxSize.slice(0, -2)*SIZE_UNITS[unit];
        }

        if(file.size <= maxSize)
            return true;

        /**
         * @event error 
         * @property {object} sender 
         * @property {object} name SizeError
         * @property {object} message 
         * @property {object} maxSize 
         * @property {object} size 
         */
        this.$emit('error', {
            sender: this,
            name: 'SizeError',
            message: '',
            maxSize: this.data.maxSize,
            size: file.size
        });

        return false;
    },
    /**
     * @method _submit() 
     * @private
     * @return {void}
     */
    _submit: function() {
        var file = this.$refs.file.files ? this.$refs.file.files[0] : {
            name: this.$refs.file.value,
            size: 0
        };

        if(!file || !file.name || !this._checkExtensions(file) || !this._checkSize(file))
            return;

        this.data._sending = true;
        /**
         * @event sending 
         * @property {object} sender 
         * @property {object} data 
         */
        this.$emit('sending', {
            sender: this,
            data: this.data.data
        });

        this.$refs.form.submit();
    },
    _onLoad: function() {
        var $iframe = this.$refs.iframe;
        var $file = this.$refs.file;

        if(!this.data._sending)
            return;
        this.data._sending = false;

        var xml = {};
        if($iframe.contentWindow) {
            xml.responseText = $iframe.contentWindow.document.body ? $iframe.contentWindow.document.body.innerText : null;
            xml.responseXML = $iframe.contentWindow.document.XMLDocument ? $iframe.contentWindow.document.XMLDocument : $iframe.contentWindow.document;
        } else if($iframe.contentDocument) {
            xml.responseText = $iframe.contentDocument.document.body ? $iframe.contentDocument.document.body.innerText : null;
            xml.responseXML = $iframe.contentDocument.document.XMLDocument ? $iframe.contentDocument.document.XMLDocument : $iframe.contentDocument.document;
        }

        if(!xml.responseText) {
            /**
             * @event error 
             * @property {object} sender 
             * @property {object} name ResponseError
             * @property {object} message 
             */
            return this.$emit('error', {
                sender: this,
                name: 'ResponseError',
                message: 'No responseText!'
            });
        }

        /**
         * @event complete 
         * @property {object} sender 
         * @property {object} xml xml
         */
        this.$emit('complete', {
            sender: this,
            xml: xml
        });

        /**
         * @event success 
         * @property {object} sender 
         * @property {object} data 
         */
        this.$emit('success', {
            sender: this,
            data: this._parseData(xml, this.data.dataType)
        });
    },
    /**
     * @method _parseData(xml, type) 
     * @private
     * @param  {object} xml xml
     * @param  {object} type 
     * @return {object|string} 
     */
    _parseData: function(xml, type) {
        if(type === 'text')
            return xml.responseText;
        else if(type === 'xml')
            return xml.responseXML;
        else if (type === 'json') {
            var data = xml.responseText;
            try {
                data = JSON.parse(data);
            } catch (e) {}

            return data;
        } else if(type === 'script')
            return eval(xml.responseText);
        else
            return xml.responseText;
    }
});

module.exports = Uploader;


