'use strict';

var Component = require('../../../ui-base/component');
var template = require('./index.html');
var _ = require('../../../ui-base/_');

var Panel = require('../../layout/panel');

/**
 * @class Collapse
 * @extend Component
 * @param {object}            [options.data]                      = 
 * @param {boolean}           [options.data.accordion=false]      => 
 * @param {boolean}           [options.data.disabled=false]       => 
 * @param {boolean}           [options.data.visible=true]         => 
 * @param {string}            [options.data.class]                => class
 */
var Collapse = Component.extend({
    name: 'collapse',
    template: template,
    /**
     * @protected
     */
    config: function() {
        _.extend(this.data, {
            panels: [],
            accordion: false
        });
        this.supr();
    }
});

module.exports = Collapse;


