/**
 * ------------------------------------------------------------
 * DatePicker 
 * @author   sensen(rainforest92@126.com)
 * ------------------------------------------------------------
 */

var Dropdown = require('../../navigation/dropdown');
var template = require('./index.html');
var _ = require('../../../ui-base/_');

var filter = require('../../../ui-base/filter');
var Calendar = require('../common/calendar');
var TimePicker = require('../time.picker');
var bowser = require('bowser');
var moment = require('moment');
var polyfill = require('../../../ui-base/polyfill');

var Validation = require('../../../util/validation');

/**
 * @class DatePicker
 * @extend Dropdown
 * @param {object}        [options.data] = 
 * @param {object}        [options.data.date=null]        <=> 
 * @param {boolean}       [options.data.showTime=false]   => 
 * @param {string}        [options.data.placeholder=''] => 
 * @param {Date|string}   [options.data.minDate=null]     => 
 * @param {Date|string}   [options.data.maxDate=null]     => 
 * @param {boolean}       [options.data.hideTip=false]    => 
 * @param {boolean}       [options.data.autofocus=false]  => 
 * @param {boolean}       [options.data.required=false]   => 
 * @param {boolean}       [options.data.readonly=false]   => 
 * @param {boolean}       [options.data.disabled=false]   => 
 * @param {boolean}       [options.data.visible=true]     => 
 * @param {string}        [options.data.size]             => , sm/md/lg
 * @param {number}        [options.data.width]            => 
 * @param {string}        [options.data.class]            => class
 */
var DatePicker = Dropdown.extend({
  name: 'date.picker',
  template: template,
  /**
   * @protected
   */
  config: function () {
    _.extend(this.data, {
      // @inherited source: [],
      // @inherited open: false,
      hideTip: false,
      minDate: null,
      maxDate: null,
      placeholder: this.$trans('PLEASE_SELECT'),
      date: null,
      _date: undefined,
      _time: undefined,
      autofocus: false,
      required: false,
      showTime: false,
      open: false
    });
    this.supr();

    this.$watch('date', function (newValue, oldValue) {

      // 
      if (typeof newValue === 'string') {
        if (bowser.msie && bowser.version <= 9)
          return this.data.date = polyfill.StringDate(newValue);
        return this.data.date = newValue ? new Date(newValue) : new Date();
      } else if (typeof newValue === 'number') {
        return this.data.date = new Date(newValue);
      }

      if (newValue == 'Invalid Date' || newValue == 'NaN')
        throw new TypeError('Invalid Date');

      // 
      if (newValue) {
        var isOutOfRange = this.isOutOfRange(newValue);
        if (isOutOfRange)
          return this.data.date = isOutOfRange;
      }

      if (newValue) {
        //this.data.date.setSeconds(0);
        this.data.date.setMilliseconds(0);
        this.data._date = new Date(newValue);
        this.data._time = filter.format(newValue, 'HH:mm:ss');
      }

      /**
       * @event change 
       * @property {object} sender 
       * @property {object} date 
       */
      this.$emit('change', {
        sender: this,
        date: newValue
      });

      this.data.tip && this.validate();
    });

    this.$watch('minDate', function (newValue, oldValue) {
      if (!newValue)
        return;

      if (typeof newValue === 'string') {
        if (bowser.msie && bowser.version <= 9)
          return this.data.date = polyfill.StringDate(newValue);
        return this.data.minDate = new Date(newValue);
      }

      if (newValue == 'Invalid Date' || newValue == 'NaN')
        throw new TypeError('Invalid Date');
    });

    this.$watch('maxDate', function (newValue, oldValue) {
      if (!newValue)
        return;

      if (typeof newValue === 'string') {
        if (bowser.msie && bowser.version <= 9)
          return this.data.date = polyfill.StringDate(newValue);
        return this.data.maxDate = new Date(newValue);
      }

      if (newValue == 'Invalid Date' || newValue == 'NaN')
        throw new TypeError('Invalid Date');
    });

    this.$watch(['minDate', 'maxDate'], function (minDate, maxDate) {
      if (!(minDate && minDate instanceof Date || maxDate && maxDate instanceof Date))
        return;

      if (minDate && maxDate && minDate - maxDate > 0)
        throw new Calendar.DateRangeError(minDate, maxDate);

      // 
      if (this.data.date) {
        var isOutOfRange = this.isOutOfRange(this.data.date);
        if (isOutOfRange)
          return this.data.date = isOutOfRange;
      }
    });

    var $outer = this.$outer;
    if ($outer && $outer instanceof Validation) {
      $outer.controls.push(this);

      this.$on('destroy', function () {
        var index = $outer.controls.indexOf(this);
        $outer.controls.splice(index, 1);
      });
    }
  },
  /**
   * @method select(date) 
   * @public
   * @param  {Date} date 
   * @return {void}
   */
  select: function (date, time) {
    if (this.data.readonly || this.data.disabled || this.isOutOfRange(date))
      return;
    this._onDateTimeChange(date, time);
    
    this._onOk();

    //this.toggle(false);
  },
  /**
   * 
   * @private
   */
  _onClose: function () {
    this.toggle(false);
  },
  _onOk: function () {
    this.data.date = this.date;
    this.data.time = this.time;
    /**
     * @event select 
     * @property {object} sender 
     * @property {object} date 
     */
    this.$emit('select', {
      sender: this,
      date: this.data.date
    });

    this.toggle(false);
  },
  /**
   * @method _onDateTimeChange(date, time) 
   * @private
   * @return {void}
   */
  _onDateTimeChange: function (date, time) {
    this.time = time || '00:00:00';
    //this.data.time
    this.date = new Date(date);
    time = this.time.split(':');
    this.date.setHours(time[0]);
    this.date.setMinutes(time[1]);
    this.date.setSeconds(time[2]);
  },
  /**
   * @method _onInput($event) 
   * @private
   * @param  {object} $event
   * @return {void}
   */
  _onInput: function ($event) {
    var value = $event.target.value;
    var date = value ? new Date(value) : null;

    if (date != 'Invalid Date')
      this.data.date = date;
    else
      $event.target.value = filter.format(this.data.date, 'yyyy-MM-dd HH:mm:ss');
  },
  /**
   * @method isOutOfRange(date) 
   * @public
   * @param {Date} date 
   * @return {boolean|Date} date false
   */
  isOutOfRange: function (date) {
    var minDate = this.data.minDate;
    var maxDate = this.data.maxDate;

    // minDate && date < minDate && minDate
    return (minDate && moment(date).isBefore(minDate, 'day') && minDate)
      || (maxDate && moment(date).isAfter(maxDate, 'day') && maxDate);
  },
  validate: function (on) {
    var data = this.data,
      date = data.date || '';

    var result = date ? Validation.validate(date.toString(), [{type: 'isDate', message: ''}]) : {success: false};
    if (data.required && !result.success) {
      result.success = false;
      result.message = this.data.message || '';
      this.data.state = 'error';
    } else {
      result.success = true;
      result.message = '';
      this.data.state = '';
    }
    this.data.tip = result.message;

    this.$emit('validate', {
      sender: this,
      on: on,
      result: result
    });

    return result;
  }
});

module.exports = DatePicker;



