'use strict';

var Component = require('../../../ui-base/component');
var template = require('./index.html');
var _ = require('../../../ui-base/_');

/**
 * @class Panel
 * @extend Component
 * @param {object}          [options.data]                    = 
 * @param {boolean}         [options.data.disabled=false]     => 
 * @param {boolean}         [options.data.visible=true]       => 
 * @param {string}          [options.data.class]              => class
 */
var Panel = Component.extend({
    name: 'panel',
    template: template,
    $tools: null,
    $foot: null,
    /**
     * @protected
     */
    config: function() {
        _.extend(this.data, {
            title: '',
            open: true
        });
        this.supr();

        if(this.$outer && this.$outer.data.panels)
            this.$outer.data.panels.push(this);
    },
    /**
     * @method toggle(open) /
     * @public
     * @param  {boolean} open /
     * @return {void}
     */
    toggle: function(open) {
        if(this.data.disabled)
            return;

        if(open === undefined)
            open = !this.data.open;

        if(open && this.$outer && this.$outer.data.panels && this.$outer.data.accordion) {
            this.$outer.data.panels.forEach(function(panel) {
                panel.data.open = false;
            });
        }

        this.data.open = open;
    },
    stopPropagation: function(e) {
        e.stopPropagation();
    }
});

module.exports = Panel;


