'use strict';

var Component = require('./component');
var _ = require('./_');
var Ajax = require('./ajax');

/**
 * @class SourceComponent
 * @extend Component
 * @deprecated
 * @param {object[]}        [options.data.source=[]]              =  
 * @param {boolean}         [options.data.updateAuto=true]          => service
 * @param {boolean}         [options.data.readonly=false]            => 
 * @param {boolean}         [options.data.disabled=false]            => 
 * @param {boolean}         [options.data.visible=true]             => 
 * @param {string}          [options.data.class]               => class
 * @param {object}          [options.service]                 @=> 
 */
var SourceComponent = Component.extend({
    service: null,
    /**
     * @protected
     */
    config: function() {
        _.extend(this.data, {
            source: [],
            updateAuto: true
        });

        if(this.data.service)
            this.service = this.data.service;

        if(this.service && this.data.updateAuto)
            this.$updateSource();

        this.supr();
    },
    request: function (options) {
        var data = this.data;
        var oldError = options.error,
            oldSuccess = options.success,
            oldComplete = options.complete;
        data.loading = true;

        options.success = function (data) {
            oldSuccess && oldSuccess(data);
            this.$update('loading', false);
        }.bind(this);
        options.error = function (data) {
            oldError && oldError(data);
            this.$update('loading', false);
        }.bind(this);

        options.complete = function (data) {
            oldComplete && oldComplete(data);
            this.$update('loading', false);
        }.bind(this);
        Ajax.request(options)
    },
    /**
     * @method getParams 
     * @protected
     * @deprecated
     * @return {object} object
     */
    getParams: function() {
        return {};
    },
    /**
     * @method $updateSource() service
     * @public
     * @deprecated
     * @return {SourceComponent} this
     */
    $updateSource: function() {
        this.service.getList.call(this, this.getParams(), function(result) {
            this.$update('source', result);
        }.bind(this));
        return this;
    }
});

module.exports = SourceComponent;


