/**
 * ------------------------------------------------------------
 * Mask     
 * @author  Cody
 * ------------------------------------------------------------
 */

const Component = require('../../../ui-base/component');
const template = require('./index.html');
const _ = require('../../../ui-base/_');

/**
 * @class Mask
 * @extend Component
 * @param {object}            [options.data]                      = 
 * @param {string}            [options.data.content]              => 
 * @param {boolean}           [options.data.closable=true]       => 
 * @param {string}            [options.data.class]                => class
 */
const Mask = Component.extend({
    name: 'mask',
    template: template,
    /**
     * @protected
     */
    config: function() {
        _.extend(this.data, {
            closable: true
        });
        this.supr();
    },
    /**
     * @protected
     */
    init: function() {
        this.supr();

        // document.body
        if(this.$root === this)
            this.$inject(document.body);
    },
    /**
     * @protected
     */
    _handleClick: function(e) {
        if (/m-mask/.test(e.target.className)) {
            if (this.data.closable) this.close();
        }
    },
    /**
     * @method close(data) 
     * @public
     * @param  {object} data 
     * @return {void}
     */
    close: function(data) {
        /**
         * @event close 
         * @property {object} data 
         */
        this.$emit('close', {
            sender: this,
            data: data
        });
        this.destroy();
    }
});

module.exports = Mask;


