/**
 * ------------------------------------------------------------
 * PopConfirm 
 * @author   ziane(zianecui@gmail.com)
 * ------------------------------------------------------------
 */

'use strict';

var dom = require('regularjs').dom;

var Component = require("../../../ui-base/component");
var template = require('./index.html');
var _ = require('../../../ui-base/_');
var trigger = require('../../layout/alignment/trigger');

/**
 * @class PopConfirm
 * @extend Component
 * @param {object}        [options.data]                        = 
 * @param {string}        [options.data.content]                => 
 * @param {string}        [options.data.contentTemplate]        => ,PopConfirmdata;
 * @param {string}        [options.data.placement=top]          => tipstop left right bottom topLeft topRight bottomLeft bottomRight leftTop leftBottom rightTop rightBottom
 * @param {string}        [options.data.okText=]             => ok
 * @param {string}        [options.data.cancelText=]         => 
 * @param {boolean}       [options.data.hideWhenScroll=false]   => window,popover
 */
var PopConfirm = Component.extend({
  name: 'pop.confirm',
  template: '<trigger ref="trigger" action="click" placement={placement} getInstance={@(this.getInstance.bind(this))} destroyOnHide=true hideWhenScroll={hideWhenScroll}>{#inc this.$body}</trigger>',
  config: function (data) {
    this.defaults({
      placement: 'top'
    });

    this.supr(data);
  },
  destroy: function() {
    if (this.data.instance) {
      this.data.instance.destroy();
    }
    this.supr();
  },
  getInstance: function() {
    if (!this.data.instance) {
      var instance = new PopUp({ data: this.data });

      instance.$on('ok', function(data) {
        if (this.events && this.events.ok) {
          this.$emit('ok', data);
        } else {
          this.data.instance.destroy();
        }

      }.bind(this));

      instance.$on('cancel', function(data) {
        if (this.events && this.events.cancel) {
          this.$emit('cancel', data);
        } else {
          this.data.instance.destroy();
        }
      }.bind(this));

      instance.$on('destroy', function() {
        this.$refs.trigger.data.isShow = false;
        this.data.instance = null;
      }.bind(this));

      this.data.instance = instance;
    }
    return this.data.instance;
  }
});

var PopUp = Component.extend({
  template: template,
  config: function (data) {
    this.defaults({
      isShow: true,
      content: '',
      contentTemplate: ''
    });
    this.supr(data);
  },
  init: function () {
    if (this.$root == this) {
      this.$inject(document.body);
    }
    this.data.element = dom.element(this);
    dom.on(this.data.element, 'click', function(e) { e.stopPropagation(); });
  },
  getElement: function () {
    return this.data.element;
  },
  ok: function () {
    var $validation = this.$refs.validation;
    if (!$validation || $validation.validate().success) {
      /**
       * @event ok 
       * @property {object} sender 
       * @property {object} data popConfirm
       */
      this.$emit('ok', {
        sender: this,
        data: this.data
      });
    }
  },
  cancel: function () {
    /**
     * @event cancel 
     * @property {object} sender 
     * @property {object} data popConfirm
     */
    this.$emit('cancel', {
      sender: this,
      data: this.data
    });
  },
});

module.exports = PopConfirm;




